import axios from 'axios'
import router from "../../routers"; // 引入路由配置
import store from "../../vuex/store";
import Qs from "querystring";
import Vue from "vue";

// axios 配置
axios.defaults.timeout = 5000;
// axios.defaults.baseURL = store.state.path;

// http request 拦截器
axios.interceptors.request.use(
    config => {
        // if (store.state.token) config.headers.Authorization = `token ${store.state.token}`;
        if(config.method=="post"){
            config.data.openid=store.state.openid;
            config.data.token=store.state.token;
           let tempData=Qs.stringify(config.data);
           config.data=tempData;
        }
        return config;
    },
    err => {
        return Promise.reject(err);
    });

// http response 拦截器
axios.interceptors.response.use(
    response => {
        //token是否验证失效
        if(response.data.error=="-1001"||response.data.error=="-1002"){
            if(store.state.token) Vue.prototype.resetUserData();
            if(axios.globalRouter.currentRoute.meta&&axios.globalRouter.currentRoute.meta.requireAuth){
                router.push({path: '/login'});
                return Promise.reject("token验证失效")
            }
        }
        response["body"]=response.data;
        return response;
    },
    error => {
        // if (error.response) {
        //     switch (error.response.status) {
        //         case 401:
        //             // 401 清除token信息并跳转到登录页面
        //             store.commit('LOGIN_OUT');
        //             router.replace({
        //                 path: 'login',
        //                 query: {redirect: router.currentRoute.fullPath}
        //             })
        //     }
        // }
        // console.log(JSON.stringify(error));//console : Error: Request failed with status code 402
        return Promise.reject(error.response.data)
    });

export default axios;



