import routes from "../../routers"; // 引入路由配置
import store from "../../vuex/store";
import { mapState } from "vuex";
import { savaToLocal, loadFromlLocal } from "./storage.js"; // 公共方法：本地缓存

export default {
  install(Vue, options) {
    Vue.prototype.updatateUserInfo = function(backfun = null) {
      //给自定义方法起个名

      Vue.prototype.$http
        .post(store.state.path + `php/ttl/userFunction.php`, {
          type: "userinfo",
          openid: store.state.openid
        })
        .then(response => {
          let data = response.body;
          if(data.error=='ok'){
            if (data.phone == null) data.phone = "";
            store.commit("UPDATE_PHONENO", data.phone);
            store.commit("UPDATE_ROLE", data.role);
            // store.commit("UPDATE_ROLE", "5");
            store.commit("UPDATE_ICON", data.headimgurl);
            // store.commit("UPDATE_USERID", data.userid);
            store.commit("UPDATE_USERNAME", data.username);
            store.commit("UPDATE_ACCOUNTS", data.accounts);
            store.commit("UPDATE_CLIENTNUM", data.clientNum);
            store.commit("UPDATE_GIFTNUM", data.giftnum);
            store.commit("UPDATE_EXPNUM", data.expnum);
            store.commit("UPDATE_SOURCE", data.source);
            // if (data.sex.toString() == "1") store.commit("UPDATE_GENDER", "nan");
            // else store.commit("UPDATE_GENDER", "nv");
          }else{
            // alert("请求用户数据data.error！=ok");
          }

          if (backfun != null && backfun != undefined) {
            backfun();
          }
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
          // alert("请求用户数据catch到错误");
        });
    };
    Vue.prototype.refreshInfoInTime = function(backfun = null) {
      //给自定义方法起个名

      Vue.prototype.$http
        .post(store.state.path + `php/Route.php`, {
          type: "userinfoheart",
          uid: store.state.userid
        })
        .then(response => {
          let data = response.body;
          if(!data.role) return;
          store.commit("UPDATE_ROLE", data.role);
          // store.commit("UPDATE_ROLE", "5");
          store.commit("UPDATE_SOURCE", data.source);
          store.commit("UPDATE_ACCOUNTS", data.accounts);
          // store.commit("UPDATE_GIFTNUM", data.giftnum);
          // store.commit("UPDATE_EXPNUM", data.expnum);

          if (backfun != null && backfun != undefined) {
            backfun();
          }
        })
        .catch(response=>{
          //请求出错处理
          console.log(response);
        });
    };
    //重置用户信息，把用户信息变成默认值
    Vue.prototype.resetUserData = function() {
        savaToLocal("aml", "openid", "");
        savaToLocal("aml", "userid", "0");
        savaToLocal("aml", "token", "");
        store.commit("UPDATA_TOKEN", "");
        store.commit("UPDATE_OPNEID", "");
        store.commit("UPDATE_USERID", "0");

        store.commit("UPDATE_ROLE", "1");
        store.commit("UPDATE_ICON", "http://www.jubenxiong.com/aml2/img/headimg/aml_logo.png");
        store.commit("UPDATE_ACCOUNTS", 0);
        store.commit("UPDATE_GIFTNUM", 0);
        store.commit("UPDATE_EXPNUM", 0);
        store.commit("UPDATE_SOURCE", 0);
    };
  }
};



